.App {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.aside {
  width: 340px;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: #efefef;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 24px 0;
  z-index: 2;
  box-shadow: 0 1px 5px 0 rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12);
}
.aside-item {
  padding: 0 24px 14px 24px;
  border-bottom: 1px solid rgba(0,0,0,.12);
}
.item-middle {
  padding-top: 24px;
}
.aside-title {
  margin: 0 0 8px;
  color: rgba(0,0,0,.87);
  font-size: 13px;
  display: block;
  font-weight: 500;
  text-align: left;
}
.asideFilterWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.switchColor {
  display: flex;
  align-items: center;
  margin-right: 25px;
}
.color {
  width: 20px;
  height: 15px;
  border-radius: 2px;
}
.switch {
  margin-left: -12px;
  margin-right: 5px;
}
.swatch {
  padding: 3px;
  background: #fff;
  border-radius: 1px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 1px;
  display: inline-block;
  cursor: pointer;
}
.popover {
  top: 0;
  position: absolute;
  z-index: 2;
}
.cover {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.inputTextField {
  margin-bottom: 10px !important;
}



.header {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}
.group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.panelWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.panel {
  width: 100%;
  height: 100vh;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
}
.canvas-wrapper {
  width: 100%;
  height: 100%;
}
.canvasWrapper{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.canvas-title {
  display: inline-block;
  margin-bottom: 30px;
  font-size: 20px;
}
.canvas {
  position: relative;

  background-color: #d8d8d8;
  border: 1px solid #b5b5b5;
  background-size: cover !important;
}
.reset {
  outline: none;
  cursor: pointer;
  position: absolute;
  top: 4px;
  left: 4px;
  background: #57ad57;
  color: #FFF;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
}
.formatButtons {
  display: flex;
  justify-content: center;
}
.formatButtons button {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  padding: 20px;
  cursor: pointer;
  background-color: #d8d8d8;
  border: 1px solid #b5b5b5;
  border-radius: 2px;
}
.formatButtons button img {
  height: 40px;
  object-fit: contain;
  margin-bottom: 10px;
}

.portrait {
  width: 500px;
  height: 500px;
}

.landscape {
  width: 237px;
  height: 500px;
}